piada y elaboran modelos cualitativos/cuantitativos para la predicción de impactos o para simular procesos medioambientales.")])])])])],1),_vm._m(7)]),_c('div',{staticClass:"row align-items-center"},[_vm._m(8),_c('div',{staticClass:"col-lg-6"},[_c('AcordionA',{attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--azul1"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Sistemas expertos"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Consiste en recoger el conocimiento profesional y el juicio de expertos en áreas temáticas específicas y de actualidad. Tal conocimiento es codificado, a través de una serie de reglas o experiencias prácticas (heurísticas), en entornos de sistemas informáticos computacionales. Los Sistemas Expertos son típicamente amigables al usuario y sólo requieren la respuesta a una serie de preguntas para conducir a un análisis particular. Se está incrementado la atención al desarrollo de sistemas expertos más exhaustivos para los procesos de EIA.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Índices o indicadores"}},[_c('div',{staticClass:"col-12"},[_c('p',[_vm._v("Se refiere a características específicas o integradas de factores medioambientales o recursos. Se utilizan dentro de los estudios de impacto para representar parámetros de amplitud de medios o recursos. Específicamente, los índices se refieren a información numérica o bien información catalogada. Se usa como sistema auxiliar para describir los ambientes afectados, así como para la predicción y evaluación de impactos.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Los índices numéricos o descriptivos se han desarrollado como una medida de la vulnerabilidad del medio ambiente y los recursos a la contaminación u otras acciones humanas y han probado su utilidad en la comparación de localizaciones para una actividad propuesta. Sobre estas bases, pueden ser formuladas las medidas para minimizar los impactos ambientales e incluir controles.")])])]),_c('div',{staticClass: